* {
    box-sizing: border-box;
    margin: 0;
    background-color: #F9ECDF;
}

body {
    font-size: 20px;
}

/* Navigation */
.nav-container {
    height: 100px;
    margin-top: 30px;
    z-index: 500;
    position: relative;
}

.wrapper {
    width: 1200px;
    margin: 0 auto;
}

nav {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    height: 150px;
}

nav ul {
    display: flex;
}

.logo {
    position: absolute;
    width: 100px;
    height: 113.65px;
    left: 60px;
    top: 20px;
}

.nav-items li {
    list-style: none;
    margin-right: 100px;
}

.nav-items li:nth-last-of-type(1) {
    margin: 0;
}

.nav-items li a {
    color: #492777;
    font-size: 25px;
    font-family: "Orelega One";
    text-decoration: none;
}

.nav-button-container {
    position: absolute;
    width: 187px;
    height: 35px;
    left: 1500px;
    top: 60px;    
}
  
.hero-content {
    position: absolute;
    height: 82px;
    left: 40.97%;
    right: 40.83%;
    top: calc(50% - 82px/2 - 177px);
    font-family: 'Orelega One';
    font-style: normal;
    font-weight: 400;
    font-size: 75px;
    line-height: 82px;
    display: flex;
    align-items: center;
    text-align: center;
    letter-spacing: 0.352431px;
    color: #492777;
  }